import React from 'react';
import styles from './userModal.module.scss';

const UserModal = ({ user, onClose }) => {
    if (!user) return null;

    return (
        <div className={styles['modal-overlay']} onClick={onClose}>
            <div className={styles['modal-content']} onClick={(e) => e.stopPropagation()}>
                <h2>{`${user.firstName} ${user.lastName} ${user.maidenName}`}</h2>
                <p><strong>: </strong>{user.age}</p>
                <p><strong>: </strong>{user.gender}</p>
                <p><strong>: </strong>{`${user.address.city}, ${user.address.address}`}</p>
                <p><strong>: </strong>{user.height} </p>
                <p><strong>: </strong>{user.weight} </p>
                <p><strong>: </strong>{user.phone}</p>
                <p><strong>Email: </strong>{user.email}</p>
                <button onClick={onClose}></button>
            </div>
        </div>
    );
};

export default UserModal;
